body {
	font-family: 'Roboto', sans-serif;
	margin: 0;
	padding: 0;
}

.signature-canvas {
	border: 1px solid #ccc;
	width: 100%;
	height: 200px;
	background-color: white; /* Ensure canvas is visible in dark mode */
}

.canvas-container {
	position: relative;
}

.button-container {
	display: flex;
	justify-content: center;
	margin-top: 20px;
}

.ton-connect-button {
	margin-left: auto;
}

.k-navbar .title {
	display: flex;
	align-items: center;
	justify-content: space-between;
	width: 100%;
}

.modal {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	display: flex;
	justify-content: center;
	align-items: center;
	background-color: rgba(0, 0, 0, 0.5);
	z-index: 1000;
}

.modal-content {
	background-color: white;
	padding: 20px;
	border-radius: 10px;
	text-align: center;
	display: flex;
	flex-direction: column;
	align-items: center;
	color: black; /* Ensure text is visible in dark mode */
}

.spinner {
	border: 4px solid rgba(0, 0, 0, 0.1);
	border-left-color: #000;
	border-radius: 50%;
	width: 36px;
	height: 36px;
	animation: spin 1s linear infinite;
	margin-bottom: 8px;
}

@keyframes spin {
	0% {
		transform: rotate(0deg);
	}
	100% {
		transform: rotate(360deg);
	}
}
